import BigNumber from "bignumber.js";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import { Button, Modal, Text, Link } from "@pancakeswap-libs/uikit";
import ModalActions from "components/ModalActions";
import ModalInput from "components/ModalInput";
import { FarmV2 } from "state/types";
import FarmV2Abi from "config/abi/farmV2.json";
import styled from "styled-components";
import { getBalanceNumber } from "utils/formatBalance";
import { oldFarmAddress } from "config/constants/farms";
import { useWeb3React } from "@web3-react/core";
import { useContract } from "hooks/useContract";
import { getAddress } from "utils/addressHelpers";
import { useApprove } from "hooks/useApprove";
import { useToast } from "state/hooks";

const Disclaimer = styled.div`
  font-size: 10px;
  color: gray;
  text-align: center;
  width: 100%;
  color: red;
`;

const ModalContainer = styled.div`
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100vw;
  height: 100vh;
  z-index: 99;
`;

const ModalBox = styled(Modal)`
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: relative;
  max-width: 550px;
`;

interface MigrationModalProps {
  oldBalance: BigNumber;
  farm: FarmV2;
  allowance?: BigNumber;
  addLiquidityUrl?: string;
  onRefresh: () => void;
}

const MigrationModal: React.FC<MigrationModalProps> = ({
  oldBalance,
  farm,
  onRefresh,
}) => {
  const [pendingAllowanceTx, setPendingAllowanceTx] = useState(false);
  const [pendingEnableTx, setPendingEnableTx] = useState(false);
  const [randomNum, setRandomNum] = useState(0);
  const [allowance, setAllowance] = useState(new BigNumber(0));
  
  const { account } = useWeb3React();
  const { toastError, toastSuccess } = useToast();

  const oldFarmContract = useContract(FarmV2Abi, oldFarmAddress);
  const newFarmContract = useContract(FarmV2Abi, getAddress(farm.farmAddress));

  useEffect(() => {
    const fetchData = async () => {
      if (account) {
        try {
          const allowed = await oldFarmContract.methods
            .allowance(account, getAddress(farm.farmAddress))
            .call();
          setAllowance(new BigNumber(allowed));
        } catch (e) {
          console.log("checking allowance fetchFailed", e);
        }
      }
    };
    fetchData();
  }, [oldBalance, account, oldFarmContract, farm.farmAddress, randomNum]);

  const { onApprove } = useApprove(oldFarmContract, newFarmContract);
  const handleEnable = async () =>{
    const tx = await newFarmContract.methods.migrateFromPreviousFarm().send({from: account});
    return tx;
  }

  const enoughAllowance = Number(allowance) >= Number(oldBalance);
  const allowanceNumber = Number(allowance);

  console.log({
    oldBalance: Number(oldBalance),
    enoughAllowance,
    allowanceNumber,
  });

  return (
    <ModalContainer>
      <ModalBox title="Protecting Your Funds - Action Required" hideCloseButton>
        <Text> Your security is our top priority.</Text>
        <br />
        <Text>
          During the past weeks, the discovery of a vulnerability / exploit led
          to our staking contract being frozen to protect your funds.
          <br />
          <br /> This exploit was patched as of July 28th 2023.
          <br />
          <br />
          To regain access to your staked balance, please perform the mandatory
          2-step moves by clicking the buttons below
          <br />
          <br />
          Thank you for your understanding.
        </Text>
        <ModalActions>
          <div>
            <Button
              width="100%"
              onClick={async ()=>{
                try {
                  setPendingAllowanceTx(true);
                  await onApprove();
                  toastSuccess("Successful Approval!")
                } catch (e) {
                  console.error(e);
                  toastError(
                    "Error", // @ts-ignore
                    e?.message || "Something went wrong",
                  );
                } finally {
                  setPendingAllowanceTx(false);
                  setRandomNum(Math.random())
            
                }
              }}
              disabled={pendingAllowanceTx || enoughAllowance}
            >
              {pendingAllowanceTx ? "Pending..." : "Approve New Contract"}
            </Button>
          </div>
          <div>
            <Button
              width="100%"
              disabled={
                pendingEnableTx || !enoughAllowance || allowanceNumber === 0
              }
              onClick={async () => {
                try {
                  setPendingEnableTx(true);
                  await handleEnable();
                  toastSuccess("Successful Enablement!")
                } catch (e) {
                  console.error(e);
                  toastError(
                    "Error", // @ts-ignore
                    e?.message || "Something went wrong",
                  );
                } finally {
                  setPendingEnableTx(false);
                  onRefresh()
                }
              }}
            >
              {pendingEnableTx ? "Pending..." : "Re-enable Staking"}
            </Button>
          </div>
        </ModalActions>

        {allowanceNumber > 0 && !enoughAllowance && (
          <Disclaimer>Approve the full amount to proceed. Click approve again</Disclaimer>
        )}

        <br />
      </ModalBox>
    </ModalContainer>
  );
};

export default MigrationModal;
